$(document).ready(function(){

    $(document).on('click', '.catalog__more-button', function(){

        var targetContainer = $('.catalog__product'),          //  –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä, –≤ –∫–æ—Ç–æ—Ä–æ–º —Ö—Ä–∞–Ω—è—Ç—Å—è —ç–ª–µ–º–µ–Ω—Ç—ã
            url =  $('.catalog__more-button').attr('data-url');    //  URL, –∏–∑ –∫–æ—Ç–æ—Ä–æ–≥–æ –±—É–¥–µ–º –±—Ä–∞—Ç—å —ç–ª–µ–º–µ–Ω—Ç—ã

        if (url !== undefined) {
            $.ajax({
                type: 'GET',
                url: url,
                dataType: 'html',
                success: function(data){

                    //  –£–¥–∞–ª—è–µ–º —Å—Ç–∞—Ä—É—é –Ω–∞–≤–∏–≥–∞—Ü–∏—é
                    $('.catalog__more').remove();

                    var elements = $(data).find('.product-row'),  //  –ò—â–µ–º —ç–ª–µ–º–µ–Ω—Ç—ã
                        pagination = $(data).find('.catalog__more');//  –ò—â–µ–º –Ω–∞–≤–∏–≥–∞—Ü–∏—é

                    targetContainer.append(elements);   //  –î–æ–±–∞–≤–ª—è–µ–º –ø–æ—Å—Ç—ã –≤ –∫–æ–Ω–µ—Ü –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞
                    targetContainer.append(pagination); //  –¥–æ–±–∞–≤–ª—è–µ–º –Ω–∞–≤–∏–≥–∞—Ü–∏—é —Å–ª–µ–¥–æ–º

                }
            })
        }
    });

});
